.header {
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100 ;
}

.header__logo {
    width: 100px;
    margin: 18px 20px 0;
    object-fit: contain;
}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%; 
}

.header__searchIcon {
    padding: 5px;
    height: 22px;
    background-color: #cd9042  ;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* flex: 1; */
    margin-right: 20px; 
}

.header__nav .header__optionLineOne {
    width: 70px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

}

.header__link {
    color: #fff;
    text-decoration: none;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__optionLineOne {
    font-size: 10px;
}

.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
    color: #fff;
    text-decoration: none;
}

.header__optionBasketCount {
    margin-left: 10px;
    margin-right: 10px ;
    font-weight: 800;
} 

.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}